#txt
{
    width: 100%;
}
.func-buttons
{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.text-summary
{
    display: block;
}
.center-align
{
    text-align: center;
}
.gradient-color
{
    background: linear-gradient(to right bottom , blue,aqua);
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
}
.btn-button
{
    background-image: linear-gradient(to right bottom,blue,aqua) ;
    color: white;
    margin: 10px;
    border: none;
    padding: 8px;
    border-radius: 5px;
}
.btn-button:hover
{
    color: white;
    font-weight: 700;
}
.btn-button:active
{
    color: white;
    font-weight: 700;
}
:root{
    --bg-color: white;
  }