*
{
    padding: 0;
    margin:0;

}
.navbar
{
 background-image: linear-gradient(to right bottom, blue,aqua) ;
 color: white;
 height:5vh;
 display: flex;
 flex-direction: row-reverse;
 align-items: center;
}

.nav-links
{
    display: inline;
    padding-left: 15px;
    padding-right: 15px;
    font-weight: 500;
    font-size: medium;
    
}
.formobile
{
    display: none;
}

@media screen and (max-width:400px) {
    .navbar
    {
        display: none;
    }
    .formobile
    {
        display: inline;
    }
   
    .nav-links
    {
        text-align: center;
        color: white;
        
    }
    .for-mobile
    {
        
        display: flex;
        flex-direction: column;
        
    }
    #mobile-hover
    {
        background-image: linear-gradient(to right bottom,blue,aqua);
    }
    
   
    
}
.material-symbols-outlined
{
  color: white;
}
